@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@200;400&display=swap);
* {
  margin: 0;
  padding: 0;
  list-style: none;
  font-family: 'Poppins', sans-serif;
  box-sizing: border-box;
  font-size: 16px;
}

body {
  background: url(/Munchkin-Game-Helper-React-App/static/media/bg.2871780f.png) no-repeat;
  background-position: bottom left;
  background-size: cover;
  background-attachment: fixed;
  height: 100vh;
  width: 96vw;
  margin: 0 auto;
  display: -ms-grid;
  display: grid;
  justify-content: center;
  align-items: flex-start;
}

#root {
  display: -ms-grid;
  display: grid;
  -ms-grid-columns: 1fr 1fr;
      grid-template-columns: 1fr 1fr;
  -ms-grid-rows: 1fr 0.2fr;
      grid-template-rows: 1fr 0.2fr;
  grid-row-gap: 15px;
  row-gap: 15px;
}

.header {
  margin-top: 30px;
  text-align: center;
  font-size: 2rem;
}

.start-game-btn {
  -ms-grid-column: 1;
  -ms-grid-column-span: 2;
  grid-column: 1/3;
  width: 100%;
  padding: 20px;
  background-color: #fff4e9;
  border: 3px solid #4b1615;
  border-radius: 10px;
  cursor: pointer;
}

.start-game-btn:hover {
  background-color: #4b1615;
  color: #fff4e9;
}

@media only screen and (max-width: 600px) {
  #root {
    width: 60vw;
    -ms-grid-columns: 1fr;
        grid-template-columns: 1fr;
    -ms-grid-rows: 1fr 0.3fr 70px;
        grid-template-rows: 1fr 0.3fr 70px;
  }
  .header {
    margin-top: 10px;
    font-size: 1.5rem;
  }
  .start-game-btn {
    -ms-grid-column: 1;
    -ms-grid-column-span: 1;
    grid-column: 1/2;
  }
}

@media only screen and (max-width: 400px) {
  body {
    width: 90vw;
    margin: 0 auto;
  }
  #root {
    width: 90vw;
  }
}
