}

.preset-container {
  display: flex;
  justify-content: flex-start;
  flex-direction: column;
  align-items: center;
  max-width: 100%;
}

.preset-container .form {
  width: 100%;
  border: 10px solid #4b1615;
  border-radius: 20px;
  padding: 20px 15px;
  background-color: #fff4e9;
}

.preset-container .form__name-input {
  position: relative;
  padding-bottom: 25px;
}

.preset-container .form__name-input input {
  font-size: 16px;
  width: 100%;
  background-color: transparent;
  border: 0;
  border-bottom: 5px solid #4b1615;
  display: flex;
  flex: 1 1;
}

.preset-container .form__name-input input:focus {
  outline: none;
}

.preset-container .form__name-input .form__error-message {
  visibility: hidden;
  position: absolute;
  color: red;
}

.preset-container .form__add-player-btn {
  width: 100%;
  padding: 10px 0;
  background-color: #fff4e9;
  border: 3px solid #4b1615;
  border-radius: 10px;
  cursor: pointer;
}

.preset-container .form__add-player-btn:hover {
  background-color: #4b1615;
  color: #fff4e9;
}

.preset-container .form .select-input {
  cursor: pointer;
  padding-bottom: 10px;
}

.preset-container .form .select-input select {
  font-size: 16px;
  width: 100%;
  outline: none;
  cursor: pointer;
  background-color: #fff4e9;
  border: 2px solid #4b1615;
  padding: 5px;
}

.preset-container .form__gender-input {
  display: flex;
  justify-content: space-between;
  padding-bottom: 10px;
}

.preset-container .form__gender-input__item {
  display: flex;
  flex: 1 1;
  justify-content: center;
}

.preset-container .form__gender-input__item input {
  display: none;
}

.preset-container .form__gender-input__item input:checked + label::before {
  height: 5px;
  width: 5px;
  border: 5px solid #4b1615;
}

.preset-container .form__gender-input__item label {
  position: relative;
  cursor: pointer;
}

.preset-container .form__gender-input__item label::before {
  position: absolute;
  content: "";
  width: 10px;
  height: 10px;
  border: 2px solid #4b1615;
  border-radius: 50%;
  left: -20px;
  top: 50%;
  transform: translateY(-50%);
}

@media only screen and (max-width: 600px) {
  .preset-container .form {
    border: 5px solid #4b1615;
  }
  .preset-container .form__name-input {
    padding-bottom: 35px;
  }
  .preset-container .form__name-input input {
    border-bottom: 2px solid #4b1615;
  }
  .preset-container .form .form__error-message {
    font-size: small;
  }
}
